
import { throttleDebounce, isMobile } from 'utils'
import { addEventListener, removeEventListener } from 'utils/globalEvent'

export interface Options {
  marginTop?: number, // 
  minMargin?: number, // 
  throttle?: number, // 
}

type compose = { line: number, margin: number } // 

/**
 * 
 * @param {number} rootWidth 
 * @param {number} innerWidth 
 * @param {number} minMargin 
 * @returns {compose}
 */
function handleCompose(rootWidth: number, innerWidth: number, minMargin: number = 0): compose {
  let line = 1 // 
  //  +  options 
  if (rootWidth <= innerWidth + minMargin * 2) return { line, margin: minMargin }

  line = Math.floor(rootWidth / innerWidth) // 

  return (function handleMargin(line: number): compose {
    if (line < 1) return { line: 1, margin: minMargin }

    const margin = (rootWidth - (innerWidth * line)) / (line + 1) // 
    if (margin < minMargin) return handleMargin(line - 1) // 

    return { line, margin }
  })(line)
}

export class Waterfall {
  private _options?: Options // 
  private _el: HTMLElement // el
  private _box: HTMLElement // 
  private _children: HTMLCollection // 
  private _pos: number // 
  private _marginTop?: number // 
  private _margin?: number // 
  private _heights: number[] // 
  public destroy: () => void // 
  constructor(el: HTMLElement, options?: Options) {
    this._options = options
    this._el = el
    this._pos = 0

    //  el 
    if (!this._el.firstElementChild) {
      throw new Error(`el need a child element`)
    }
    this._box = this._el.firstElementChild as HTMLElement
    this._box.style.position = 'relative'
    this._children = this._box.children

    this._heights = []

    this._handleOptions()

    if (!isMobile()) { // 
      const resize = throttleDebounce(this.reset.bind(this, 200), (options?.throttle ?? 200))
      addEventListener('window', 'resize', resize)
      this.destroy = () => {
        this._el.remove()
        removeEventListener('window', 'resize', resize)
      }
    } else {
      this.destroy = () => {
        this._el.remove()
      }
    }
  }

  // options
  private _handleOptions(): void {
    if (!this._children.length) return
    const width = this._el.clientWidth
    const childWidth = (this._children[0] as HTMLElement).offsetWidth

    const { marginTop, minMargin } = this._options ?? {}
    const { line, margin } = handleCompose(width, childWidth, minMargin) // 

    this._margin = margin
    this._marginTop = marginTop == null ? margin : marginTop // 
    this._heights = new Array(line).fill(0) // 
  }

  /**
   *  pos 
   * @param {number} transition 
   */
  private _layout(transition: number = 0): void {
    const { _box, _children, _heights, _marginTop, _margin } = this
    if (!_children[0]) {
      _box.style.cssText +=
        `
          height: 0px;
          transition: all ${transition}ms;
        `
      return
    }
    const childWidth: number = (_children[0] as HTMLElement).offsetWidth

    for (; this._pos < this._children.length; ++this._pos) {
      handle(this._pos)
    }

    /**
     * 
     * @param {number} i 
     */
    function handle(i: number): void {
      window.requestAnimationFrame(() => {
        (_children[i] as HTMLElement).style.cssText += `position: absolute;` // 

        const insert = _heights.indexOf(Math.min(..._heights)); // 
        (_children[i] as HTMLElement).style.cssText += // 
          `
          transition: all ${transition}ms;
          transform: translate3d(${_margin! * (insert + 1) + childWidth * insert
          }px,${_marginTop! + _heights[insert]
          }px,0);
        `

        // 
        _heights[insert] += _marginTop! + (_children[i] as HTMLElement).offsetHeight
        _box.style.cssText +=
          `
          height: ${Math.max(..._heights) + _margin!}px;
          transition: all ${transition}ms;
        `
      })
    }
  }

  /**
   * 
   * @param {number} transition 
   */
  public reset(transition: number = 0): void {
    this._pos = 0 // 
    this._handleOptions() // 
    this._layout(transition) // 
  }

  /**
   * 
   */
  public update(): void {
    this._pos ? this._layout() : this.reset()
  }
}
